<template>
  <v-flex xs12 sm12 md12>
    <v-card height="100%">
      <v-card-title primary-title>
        <div>
          <h3>Seat Randomizer</h3>
        </div>
      </v-card-title>
      <v-card-text>
        <div>
          <v-text-field
            v-model="player_n"
            label="# of Players"
            placeholder="4"
            outlined
            dense
            type="number"
            prepend-icon="mdi-account"
            append-outer-icon="mdi-check"
            :rules="[rules.players]"
            v-on:keyup.enter="result = randomizeSeat(player_n)"
            @click:append-outer="result = randomizeSeat(player_n)"
          >
          </v-text-field>
        </div>
        <v-divider></v-divider>
        <v-randomizer-result
          :result="result"
        />
        <v-list></v-list>
      </v-card-text>
    </v-card>
  </v-flex>
</template>

<script>
import RandomizerResult from '@/components/tools/RandomizerResult.vue';

export default {
  components: {
    'v-randomizer-result': RandomizerResult
  },
  data() {
    return {
      result: undefined,
      player_n: '4',
      rules: {
        players: value => {
          return Number(value) > 0 || "Invalid Input"
        }
      }
    }
  },
  methods: {
    randomizeSeat(n) {
      const result = { sitIn: [], sitOut: [] };
      const arr = Array(Number(n)).fill(0).map((e, i) => i + 1)
      const shuffled = this.chunk(this.shuffle(arr));
      shuffled.forEach(e => {
        if (e.length === 4) {
          e.sort((a, b) => Number(a) - Number(b));
          result.sitIn.push(e);
        } else {
          result.sitOut.push(e);
        }
      });
      return result;
    },
    shuffle(input) {
      const array = [...input];
      for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    },
    chunk(input) {
      const array = [...input];
      const size = 4;
      return array.reduce((all, one, i) => {
        const ch = Math.floor(i / size);
        all[ch] = [].concat((all[ch] || []), one);
        return all;
      }, []);
    }
  }
};
</script>

